import Cell from '../../../components/Cell/Cell.vue'
import { mapMutations } from 'vuex'
import { computedRequired } from '../../../utils/utils.js'
import JobModel from '@/models/job.js'
const jobModel = new JobModel()
export default {
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			id: '',
			jobInfo:{
				storeName: ' ',
				num: ' ',
				address: ' ',
				jobName: ' ',
				description: ' ',
				officialSalary: ' ',
				avgSalary: ' ',
				reward: ' ',
				interviewTime: ' ',
				overtime: ' '
			},
			loginType: ''
		}
	},
	onLoad(option){
		this.id = option.id
		this.getJobDetail()
		this.loginType = uni.getStorageSync('loginType')
	},
	components: {
		Cell
	},
	filters: {
		filterProbation(data){
			if(data.probation === 0){
				return '无试用期'
			}
			const trialTime = data.trialTime == 0 ? '一个月' : data.trialTime == 1 ? '两个月' : '三个月'
			return `时间：${trialTime} | 薪资: ${data.trialSalary|| ' '}`
		},
		filterOverTime(time){
			const overtime = time == 0 ? '7天' : time == 1 ? '15天' : '30天'
			return `${overtime}`
		}
	},
	computed: {
		required(){
			return computedRequired(this.jobInfo)
		}
	},
	onShareAppMessage(e) {
		
	},
	methods: {
		...mapMutations(['switchTab']),
		getJobDetail(){
			uni.showLoading({
				mask: true
			})
			jobModel.jobDetail(this.id).then(res=>{
				uni.hideLoading()
				const { code, data, message } = res.data
				if(code === '0'){
					this.jobInfo = data
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '获取岗位详情失败'
				})
			})
		},
		recceiveOrder(){
			const { id } = this.jobInfo
			uni.showLoading({
				mask:true
			})
			jobModel.collectionJob(id).then(res=>{
				uni.hideLoading()
				const {code, message, data } = res.data
				if(code === '0'){
					// 执行接单成功后续动作
					uni.showModal({
						title: '',
						content: '接单成功！\r\n是否立即推荐候选人',
						confirmText: '立即推荐',
						confirmColor: '#ff9058',
						cancelText: '再等等吧',
						cancelColor: '#ff9058',
						success: (response)=>{
							if(response.confirm){
								this.switchTab({index: 2,jobId:id})
								uni.reLaunch({
									url: '../../customer/main/main'
								})
								
							}else if(response.cancel){
								uni.showModal({
									title: '',
									content: '您可进入【我的项目】继续操作',
									confirmText: '知道了',
									confirmColor: '#ff9058',
									showCancel: false
								})
							}
						},
						fail: err=>{
							console.log(err)
						}
					})
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '操作失败，请稍后再试'
				})
			})
		},
		share(){
			uni.share({
			    provider: "weixin",
			    scene: "WXSenceTimeline",
			    type: 0,
			    href: "http://uniapp.dcloud.io/",
			    title: "标题",
			    summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
			    imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
			    success: function (res) {
			    	console.log("success:" + JSON.stringify(res));
			    },
			    fail: function (err) {
			    	console.log("fail:" + JSON.stringify(err));
			    }
			});
		},
		recommend(){
			this.switchTab({index: 2,jobId:id})
			uni.reLaunch({
				url: '../../customer/main/main'
			})
		},
		unShelveJob(){
			this.updatsStatus(2)
		},
		shelveJob(){
			this.updatsStatus(1)
		},
		updatsStatus(status){
			const that = this
			const { id } = this.jobInfo
			uni.showLoading({
				mask: true
			})
			jobModel.updateStatus(id, status).then(res=>{
				uni.hideLoading()
				uni.showToast({
					title: '操作成功'
				})
				const {code, message, data } = res.data
				if(code === '0'){
					that.jobInfo.status = status
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '操作失败，请稍后再试'
				})
			})
		},
		modifyJob(){
			uni.navigateTo({
				url: '../addJob/addJob?id='+ this.jobInfo.iid
			})
		}
	}
}
